// API call to send code to job queue
export async function executeCode(c: string, l: string) {
  const response = await fetch('http://localhost:8000/api/submit_code', {
    method: 'POST',
    headers: { 'Content-Type': 'application/json' },
    body: JSON.stringify({
      code: c,
      language: l
    }),
  });
  return await response.json();
}

// API call to get code output based off job_id
export async function getJob(job_id: string) {
  const response = await fetch(`http://localhost:8000/api/get_result/${job_id}`, {
    method: 'GET',
    headers: { 'Content-Type': 'application/json' },
  });
  return await response.json();
}